              type="primary"
              icon="el-icon-edit"
              @click="touchUser"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              circle
              @click="DeleteUser(scope.row.workID)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        :current-page="1"
        :page-sizes="[5, 15, 25, 30]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="200"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%"
    >
     <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="">
    <el-input v-model="form.name"></el-input>
  </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >

        <el-button
          type="primary"
          @click="fanhui"
        ></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<style lang="less" scoped>
</style>

<script>
let i = 0
