    },
    touchUser() {
      this.dialogVisible = true;
    },
    fanhui(){
       this.$message.success("");
       this.dialogVisible = false
    },
    DeleteUser(id){
      for(i in this.tableData){
        if(this.tableData[i].workID==id){
          this.tableData.splice(i,1)
        }
      }
      this.$message.success('') 
    }
