              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          <el-col
            class="line"
            :span="1"
          >--</el-col>
          <el-col :span="8">
            <el-date-picker
              type="date"
              placeholder=""
              v-model="form.date2"
              style="width: 100%; margin-left:-25px"
            ></el-date-picker>
          </el-col>
        </el-form-item>
        <small>  </small>
        <el-select
          size="mini"
