        size='small'
        :lazy='true'
      >
        <el-table-column
          prop="workID"
          label="ID"
          width="130"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="activityName"
          label=""
          width="130"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="activityContent"
          label=""
          width="120"
          align="center"
        >
        </el-table-column>

        <el-table-column
          prop="publicTime"
          label=""
          width="140"
          align="center"
        >
        </el-table-column>

        <el-table-column
          prop="publicTime2"
          label=""
          width="140"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="DateTime"
          label=""
          align="center"
        >

        </el-table-column>
        <el-table-column
          prop="DateTime1"
          label=""
          align="center"
        >

        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          align="center"
        >
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              circle
              @click="editUser(scope.row)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              circle
              @click="DeleteUser(scope.row.workID)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        :current-page="1"
        :page-sizes="[5, 15, 25, 30]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="200"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%"
    >
      <el-form
        ref="getUserInfo"
        :model="getUserInfo"
        label-width="80px"
      >
        <el-form-item label="">
          <el-input v-model="getUserInfo.activityName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="getUserInfo.activityContent"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="getUserInfo.publicTime"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="getUserInfo.publicTime2"></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="commite()"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<style lang="less" scoped>
small {
  margin-left: 12px;
  color: rgb(90, 86, 86);
}
</style>

<script>
let i = 0;
export default {
  data() {
    return {
      form: {
        date1: ""
      },
      value1: [],
      options1: [
        {
          value: "1",
          label: "Vip"
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
          label: ""
        },
        {
          value: "5",
          label: ""
        }
      ],
      tableData: [
        {
          workID: "45611121",
          activityName: "",
          activityContent: "",
          publicTime: "VIP",
          publicTime2: "13845614562",
          DateTime: "2020-05-09",
          DateTime1: "520"
        },
        {
          workID: "45611122",
          activityName: "",
          activityContent: "",
          publicTime: "VIP",
          publicTime2: "13845614562",
          DateTime: "2019-07-16",
          DateTime1: "113"
        },
        {
          workID: "45611123",
          activityName: "",
          activityContent: "",
          publicTime: "",
          publicTime2: "13845614562",
          DateTime: "2019-09-10",
          DateTime1: "50"
        },
        {
          workID: "45611124",
          activityName: "",
          activityContent: "",
