          activityContent: "",
          publicTime: "VIP",
          publicTime2: "13845614562",
          DateTime: "2020-01-01",
          DateTime1: "44"
        },
        {
          workID: "45611126",
          activityName: "",
          activityContent: "",
          publicTime: "",
          publicTime2: "13845614562",
          DateTime: "2019-08-01",
          DateTime1: "120"
        },
        {
          workID: "45611127",
          activityName: "",
          activityContent: "",
          publicTime: "",
          publicTime2: "13845614562",
          DateTime: "2020-05-09",
          DateTime1: "51"
        }
      ],
      dialogVisible: false,
      getUserInfo: []
    };
  },
  methods: {
    search() {
      this.$message.success("");
      this.tableData[0].activityName = "";
      this.tableData[1].activityName = "";
      this.tableData[2].activityName = "";
      this.tableData[3].activityName = "";
      this.tableData[4].activityName = "";

      this.tableData[5].activityName = "";
      this.tableData[6].activityName = "";

      for (i in this.tableData) {
        this.tableData[i].publicTime = "VIP";
      }
      console.log(this.tableData.length);
    },
    editUser(userInfo) {
      this.dialogVisible = true;
      this.getUserInfo = userInfo;
    },
    commite() {
      this.$message.success("");
      this.dialogVisible = false;
    },
    DeleteUser(id) {
      this.$message.success("");
      for (i in this.tableData) {
        if (this.tableData[i].workID == id) {
          this.tableData.splice(i, 1);
        }
      }
    }
  }
};
</script>